<template>
  <div v-loading="loading" class="i-attrs-editor">
    <el-tabs
      v-model="activeTab"
      class="i-attrs-editor__tabs"
      :before-leave="beforeLeave"
      @tab-click="onTabChange"
    >
      <el-tab-pane label="常规" name="0"/>
      <el-tab-pane label="数据" name="1"/>
      <el-tab-pane label="初始接口" name="2"/>
      <el-tab-pane label="显隐" name="3"/>
      <el-tab-pane label="子节点" name="4"/>
    </el-tabs>
    <template v-if="visibleTabs[activeTab]">
      <div class="i-attrs-editor__contents">
        <el-tabs
          v-if="activeTab === '4'"
          v-model="childType"
          tab-position="right"
          type="border-card"
          class="i-attrs-editor__nodes"
          @tab-click="onTabChange"
          :before-leave="beforeLeave"
        >
          <el-tab-pane v-if="visibleNodes.header" label="头部" name="header"/>
          <el-tab-pane v-if="visibleNodes.body" label="主体" name="body"/>
          <el-tab-pane v-if="visibleNodes.footer" label="底部" name="footer"/>
          <el-tab-pane v-if="visibleNodes.controls" label="表单项" name="controls"/>
          <el-tab-pane v-if="visibleNodes.columns" label="表格列" name="columns"/>
          <el-tab-pane v-if="visibleNodes.panels" label="面板" name="panels"/>
          <el-tab-pane v-if="visibleNodes.actions" label="行为组" name="actions"/>
        </el-tabs>
        <div
          v-if="!loading"
          class="i-attrs-editor__content"
          :class="{
            nodes: activeTab === '4'
          }"
        >
          <i-form
            classname="i-attrs-editor__form"
            ref="normalFormRef"
            size="small"
            label-width="110px"
            :path="`/props-editor/${activeTab}/node/${childType}`"
            :init-data="formdata"
            :inherit="{type: 'deep'}"
            :controls="controls"
          />
        </div>
      </div>
    </template>
    <el-empty v-else :description="`【${chName}】不支持该配置`" />
    <el-drawer
      v-model="showChildNode"
      title="子节点编辑中"
      size="50%"
      destroy-on-close
      lock-scroll
      :before-close="submitForm"
      custom-class="i-toolmaker__drawer__container"
    >
      <div class="i-toolmaker__drawer__content attrs-editor">
        <i-form
          ref="childFormRef"
          size="small"
          label-width="110px"
          :path="`${path}/props-editor/${activeTab}/node/${childType}`"
          :init-data="childFormdata"
          :controls="childControls"
        />
      </div>
      <div class="i-toolmaker__drawer__footer">
        <el-button
          size="small"
          type="primary"
          @click="showChildNode = false"
        >
          取 消
        </el-button>
        <el-button
          size="small"
          type="danger"
          @click="submitForm"
        >
          {{ loading ? '提交中 ...' : '确 定' }}
        </el-button>
      </div>
    </el-drawer>
  </div>
</template>
<script>
import {defineComponent, getCurrentInstance, onBeforeMount, ref, onBeforeUnmount, nextTick, onMounted} from 'vue';
import {cloneDeep} from 'lodash';
import componentMap from '../../hooks/useComponentMap';
import { initData } from '../attrs/init-data.js';
import { initApi } from '../attrs/init-api.js';
import { visible } from '../attrs/visible.js';
import { childNode } from '../attrs/child-node.js';
export default defineComponent({
  name: 'AttrsEditor',
  props: {
    path: {
      type: String,
      required: true,
    },
    name: {
      type: String,
      required: true,
    },
    activeRenderer: {
      type: String,
      required: true,
    },
    activeJson: {
      type: Object,
      required: true,
    },
    editTabChange: {
      type: Function,
      required: true,
    },
    nodeChange: {
      type: Function,
      required: true,
    }
  },
  setup(props) {
    const { proxy } = getCurrentInstance();
    const loading = ref(false);
    const childType = ref('body');
    const showChildNode = ref(false);
    const formdata = ref();
    const childFormdata = ref();
    const controls = ref();
    const childControls = ref();
    const activeTab = ref('0');
    const tabs = {
      'mixinProps': '0',
      'initData': '1',
      'initApi': '2',
      'visible': '3'
    };
    const visibleTabs = {};
    const visibleNodes = {};
    let eachFormdata = {};
    let attrs = [initData, initApi, visible, childNode];
    let chName = componentMap[props.name].chName;
    const updateForm = () => {
      let temps;
      formdata.value = {};
      controls.value = [];
      temps = activeTab.value === '4' ? attrs[activeTab.value][childType.value](props.activeJson.renderer) : attrs[activeTab.value];
      temps.forEach(item => {
        formdata.value[item.name] = props.activeJson[item.name] || item.defaultValue;
      });
      Object.assign(formdata.value, eachFormdata);
      controls.value = temps;
      loading.value = false;
    };
    const beforeLeave = () => {
      const form = proxy.$refs.normalFormRef;
      if (form && form.state && form.state.data) {
        eachFormdata = form.state.data;
      }
    };
    const onTabChange = target => {
      const tabIndex = Number(target.props.name);
      if (tabIndex === tabIndex) {
        props.editTabChange(target.props.name);
      } else {
        props.nodeChange(target.props.name);
      }
      loading.value = true;
      const timer = setTimeout(() => {
        clearTimeout(timer);
        updateForm();
      }, 500);
    };
    const onEditDeep = (index) => {
      const {state: {data}} = proxy.$refs.normalFormRef;
      showChildNode.value = true;
      childFormdata.value = data[childType.value][index];
      childControls.value = proxy.$.appContext.$iAttrs[`i-${childFormdata.value.renderer}`].default;
      proxy.$.appContext.$iAttrs[`i-${childFormdata.value.renderer}`].default.forEach(item => {
        if (!childFormdata.value.hasOwnProperty(item.name)) {
          childFormdata.value[item.name] = item.defaultValue;
        }
      });
    };
    const submitForm = () => {
      const {$refs: {form}, state: {data}} = proxy.$refs.childFormRef;
      let pureForm = {};
      form.validate((valid) => {
        if (valid) {
          for (let key in data) {
            if (data.hasOwnProperty(key) && data[key] !== '') {
              pureForm[key] = data[key];
            }
          }
          Object.assign(childFormdata.value, pureForm)
          showChildNode.value = false;
        }
      });
    };
    onBeforeMount( () => {
      let nodeList = ['header', 'body', 'footer', 'controls', 'columns', 'panels', 'actions'];
      attrs.unshift(cloneDeep(proxy.$.appContext.$iAttrs[`i-${props.name}`].default));
      proxy.$.appContext.components[`i-${props.name}`].mixins.forEach(item => {
        visibleTabs[tabs[item.name]] = true;
      });
      if (proxy.$formItems.includes(`i-${props.name}`)) {
        visibleTabs[tabs.visible] = true;
      }
      let visibleNodeType = false;
      for (let i = 0; i < nodeList.length; i++) {
        let item = nodeList[i];
        if (proxy.$.appContext.components[`i-${props.name}`].mixins[0].props.hasOwnProperty(item)) {
          visibleNodeType = true;
          visibleNodes[item] = true;
          childType.value = item;
        } else {
          visibleNodes[item] = false;
        }
      }
      visibleTabs['4'] = visibleNodeType;
      updateForm();
    });
    onMounted(() => {
      proxy.$eventHub.$on('component:attrs-editor', onEditDeep);
    });
    onBeforeUnmount(() => {
      proxy.$eventHub.$off('component:attrs-editor', onEditDeep);
      formdata.value = {};
      attrs.shift();
    });
    return {
      beforeLeave,
      onTabChange,
      submitForm,
      showChildNode,
      childFormdata,
      activeTab,
      childType,
      formdata,
      controls,
      childControls,
      loading,
      visibleTabs,
      visibleNodes,
      attrs,
      chName
    };
  },
});
</script>
