let rendererList = Object.keys(rendererMap).map(key => {
  const img = key.toLowerCase().replace(/( |^)[a-z]/g,(L)=>L.toUpperCase());
  return {
    value: `${key}`,
    text: rendererMap[key].chName,
    disabled: rendererMap[key].disabled,
    body: [
      {
        renderer: 'image',
        fit: 'fill',
        src: `https://songshuzhong.github.io/umis-website/public/thumbnail/${img}.svg`
      },
      {
        renderer: 'html',
        html: rendererMap[key].chName,
      }
    ],
  };
});
