<template>
  <section class="i-toolmaker-wrapper">
    <section
      class="i-toolmaker__container"
      @click.capture.stop="handleClick"
    >
      <i-schema
        v-if="schemaInstance"
        :key="updatedCount"
        :init-schema="schemaInstance"
      />
    </section>
    <section
      v-if="state.activeNode"
      class="i-toolmaker__wedgets"
      :style="{
        top: `${state.clientY - 40}px`,
        left: `${state.clientX}px`
      }"
    >
      <el-button-group
        size="small"
        type="primary"
        class="i-toolmaker__tools"
      >
        <el-button
          plain
          icon="Edit"
          @click="onEdit"
        />
        <el-button
          :disabled="state.activeTrack === '/page'"
          plain
          icon="Top"
          @click="onTop"
        />
        <el-button
          :disabled="state.activeTrack === '/page'"
          plain
          icon="Bottom"
          @click="onBottom"
        />
        <el-button
          plain
          icon="View"
          @click="onDebug"
        />
        <el-button
          :disabled="state.activeTrack === '/page'"
          plain
          icon="Delete"
          @click="onDelete"
        />
      </el-button-group>
    </section>
    <el-drawer
      v-model="state.drawerVisible"
      :title="`当前正在编辑：『${state.chName}』`"
      size="50%"
      destroy-on-close
      append-to-body
      lock-scroll
      custom-class="i-toolmaker__drawer__container"
    >
      <div class="i-toolmaker__drawer__content">
        <attrs-editor
          ref="propsEditorRef"
          :path="path"
          :name="state.name"
          :active-json="state.activeJson"
          :active-renderer="state.activeRenderer"
          :edit-tab-change="onEditTabChange"
          :node-change="onNodeChange"
        />
      </div>
      <div class="i-toolmaker__drawer__footer">
        <el-button
          size="small"
          type="primary"
          :loading="loading"
          @click="submitForm"
        >
          {{ loading ? '提交中 ...' : '确 定' }}
        </el-button>
      </div>
    </el-drawer>
    <el-dialog
      v-model="showDebug"
      title="值域："
      destroy-on-close
    >
      <i-data
        class="i-toolmaker-wrapper__debug"
        :path="`${path}/${state.activeTrack}`"
        :init-data="state.data"
      />
    </el-dialog>
  </section>
</template>
<script>
import {defineComponent, watch, getCurrentInstance, reactive, ref} from 'vue';
import {purifyObject} from '../../utils/tpl';
import useDropTools from '../../hooks/useDropTools';
import AttrsEditor from './attrs-editor.vue';
import componentMap from '../../hooks/useComponentMap';
const requireAttrs = require.context('../attrs', true, /[\w-]+\.js$/);
const iAttrs = {};
requireAttrs.keys().forEach(filePath => {
  const attrConfig = requireAttrs(filePath);
  let attrName = filePath.replace(/(.*\/)*([^.]+).*/gi, '$2');
  iAttrs[`i-${attrName}`] = attrConfig;
});
export default defineComponent({
  name: 'Toolmaker',
  components: {
    AttrsEditor,
  },
  setup(props) {
    const { proxy } = getCurrentInstance();
    const {findMisComponent, beforeJSONEdit, swapOneInJSON, schemaInstance} = useDropTools(props);
    const loading = ref(false);
    const showDebug = ref(false);
    const updatedCount = ref(0);
    const path = '/i-editor/toolmaker';
    const state = reactive({
      activeTab: '',
      nodeType: 'body',
      activeRenderer: '',
      name: '',
      chName: '',
      activeNode: '',
      drawerVisible: false,
      activeJson: {},
      activeTrack: '',
      clientY: '',
      clientX: ''
    });
    const handleClick = (e) => {
      const node = findMisComponent(e.target);
      const track = node.getAttribute('track');
      const path = node.getAttribute('path');
      track && proxy.$eventHub.$emit('component:state', path, data => {
        state.data = data;
      });
      if (!track || state.activeNode == node) {
        onClear();
      } else if (state.activeTrack !== track) {
        state?.activeNode?.classList?.remove('i-toolmaker__active');
        state.activeTrack = track;
        state.activeNode = node;
        state?.activeNode?.classList?.add('i-toolmaker__active');
      }
      state.clientY = e.clientY;
      state.clientX = e.clientX;
    };
    const submitForm = () => {
      const {$refs: {form}, state: {data}} = proxy.$refs.propsEditorRef.$refs.normalFormRef;
      let pureForm = {};
      form.validate((valid) => {
        if (valid) {
          const model = data;
          for (let key in model) {
            if (model.hasOwnProperty(key)) {
              pureForm[key] = model[key];
            }
          }
          if (state.activeTab !== '5') {
            const defaultModel = {};
            proxy.$.appContext.$iAttrs[`i-${state.name}`].default.forEach(item => {
              defaultModel[item.name] = item.defaultValue;
            });
            Object.assign(defaultModel, pureForm)
          }
          Object.assign(state.activeJson, pureForm);
          purifyObject(state.activeJson, proxy.$.appContext.components[`i-${state.activeRenderer}`].mixins);
          updatedCount.value++;
          state.drawerVisible = false;
        }
      });
    };
    const onClear = () => {
      state?.activeNode?.classList?.remove('i-toolmaker__active');
      state.nodeType = 'body';
      state.activeNode = '';
      state.activeTrack = '';
      state.data = {};
    };
    const onEditTabChange = (name) => {
      state.activeTab = name;
    };
    const onNodeChange = name => {
      state.nodeType = name;
    };
    const onEdit = () => {
      const name = state.activeNode.getAttribute('renderer');
      if (!proxy.$.appContext.components[`i-${name}`]) {
        return proxy.$message({
          message: '找不到对应的渲染器',
          showClose: true,
          grouping: true,
          type: 'warning'
        });
      }
      const target = beforeJSONEdit();
      state.activeJson = target.json;
      state.activeRenderer = name;
      state.name = name.toLowerCase();
      state.chName = componentMap[state.name].chName;
      state.drawerVisible = true;
    };
    const onDebug = () => {
      showDebug.value = true;
    };
    const onBottom = () => {
      const { pJson, trackIndex } = beforeJSONEdit();
      if (trackIndex + 1 !== pJson.length) {
        swapOneInJSON(pJson, trackIndex + 1, trackIndex);
      } else {
        proxy.$message('已经处于底层，无法下移！');
      }
      onClear();
    };
    const onTop = () => {
      const { pJson, trackIndex } = beforeJSONEdit();
      if (trackIndex !== 0) {
        swapOneInJSON(pJson, trackIndex, trackIndex - 1);
      } else {
        proxy.$message('已经处于顶层，无法上移！');
      }
      onClear();
    };
    const onDelete = () => {
      const { pJson, trackIndex } = beforeJSONEdit();
      pJson.splice(trackIndex, 1);
      onClear();
    };
    watch(() => state.drawerVisible, (val) => {
      if (!val) {
        onClear();
      }
    });
    proxy.$.appContext.$iAttrs = iAttrs;
    return {
      onEditTabChange,
      onNodeChange,
      submitForm,
      handleClick,
      onClear,
      onEdit,
      onDebug,
      onBottom,
      onTop,
      onDelete,
      componentMap,
      schemaInstance,
      updatedCount,
      showDebug,
      loading,
      state,
      path
    };
  },
});
</script>
